<template>
    <div>
        <template-store  :usedbutton="false" :viewbutton="true"  @viewtemplateconfig="(index) => viewtemplate(index)"/>
        <Modal v-model="showTemplateCenter"  fullscreen  >
            <template-center   :use-type="useType" :use-template-index="useTemplateIndex" ref="TemplateController"/>
            <div slot="footer" >
            </div>
        </Modal>
    </div>
</template>
<script>
import TemplateStore from '../temp/store';
import TemplateCenter from '../temp/index';
export default {
    name: 'StoreForward',
    components: {
        TemplateStore,
        TemplateCenter
    },
    data () {
        return {
            showTemplateCenter: false,
            useTemplateIndex: -1,
            useType: ''
        };
    },
    methods: {
        viewtemplate (index) {
            this.useType = 'view';
            this.useTemplateIndex = index;
            this.showTemplateCenter = true;
            this.$refs.TemplateController.useTemplate(index);
        }
    }
};
</script>
